import React from "react";
import { Link, useNavigate } from "react-router-dom";
import listaItensMenu,{ comumUsuarioLIstaData} from "../Sidebar/data/listaMenu";
import { SidebarStyle, Item, BtnEsconder } from "./styles";
import DarkButton from "../Buttons/ButtonDark";
import { useSelector, useDispatch } from "react-redux";
import { setEsconder } from "../../Redux/actions/menuMobileAction";
import { getDataSession } from "../../services/getDataSession";


const SidebarMobile: React.FC = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const exitEndLogout = () => {
    navigate('/');
    localStorage.clear();
  };

  
  const { isAdmin } = getDataSession();

  
  let listaMenu = listaItensMenu;

  if (!isAdmin) {
    listaMenu = comumUsuarioLIstaData;
  } 

  function esconderSidebar() {
    dispatch(setEsconder())
  }

  function exitWhenChangePage(elem:any) {
    let isClicked = elem.target;
    // console.log(isClicked)
    if (isClicked) {
      dispatch(setEsconder())
    }

}

  const menuMobile = useSelector((store: any) => store?.menuMobile);
  return (
    <>
      {menuMobile && (
        <SidebarStyle onClick={exitWhenChangePage}>
          <div>
            <BtnEsconder onClick={() => esconderSidebar()}>X</BtnEsconder>
            <ul>
              {listaMenu.map((item) => (
                <Item key={item.id}>
                  <Link to={item.path}>{item.descricao}</Link>
                </Item>
              ))}
            </ul>
          </div>
          <DarkButton onClick={exitEndLogout}>Sair</DarkButton>
        </SidebarStyle>
      )}
    </>
  );
};

export default SidebarMobile;
