import React from "react";
import { CheckBoxWrapper, CheckBox, CheckBoxLabel } from './styles'

type SwitchData = {
    checked?:React.HTMLInputTypeAttribute | any;
    onChange?:React.ChangeEventHandler | any;
}

const Switch:React.FC<SwitchData> = ({
    checked,
    onChange
}) => {
  return (
    <CheckBoxWrapper>
      <CheckBox
        id="switch-shadow"
        type="checkbox"
        checked={checked}
        onChange={onChange}
      ></CheckBox>
      <CheckBoxLabel htmlFor="checkbox" />
    </CheckBoxWrapper>
  );
}

export default Switch;
